$img_spacing: 5px;
$img_height:350px;

.img_row{
    height: $img_height;
    width: 100%;
    overflow: hidden;
    box-sizing:border-box;
    padding: $img_spacing;
    }
    .col{ 
    width: 100%;
    height: 100%;
    float: left;
    object-fit: cover;
    box-sizing:border-box;
    padding: $img_spacing;
    }
    .right{
    float: right;
    }
    .one {
    width:33.33%;
    }
    .two {
    width: 66.66%;
    }
    .three{
    width: 100%;
    }